<template>

        <SuccessError v-if="success" type="success" message="Dashboard created successfully! You will be redirected to your dashboards shortly."/>
        <SuccessError v-else-if="error" type="error" :message="error" />

        <div v-else class="px-4">
          <SectionTitleLineWithButton :icon="mdiMonitorDashboard" title="Dash Studio (beta)" />
              <CardBox>   
              <MyDashboardsIntro />
            </CardBox>
            <SectionTitleLineWithButton :icon="mdiMonitorDashboard" title="Create a new dashboard" />

            <div class="py-2">
              <CardBox>
              <form @submit.prevent="submitForm">

                <SectionTitleLineWithButton :icon="mdiHomeAccount" title="General info" />
                  <div class="w-full py-4 flex justify-center text-center inline-flex items-center">
                              <div class="w-2/6 px-2"> 
                                <input type="text" v-model="dashboardName" id="dashboardname" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Dashboard name" required />
                              </div>

                              <div class="w-3/6">
                                <input type="text" v-model="dashboardDescription" id="dashboarddescription" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Dashboard description" required />
                              </div>

                              <div class="px-2 w-1/6">
                                <label class="relative inline-flex items-center mr-5 cursor-pointer">
                                  <input type="checkbox" v-model="publicDashboard" class="sr-only peer">
                                  <div class="w-11 h-6 bg-gray-200 rounded-full peer dark:bg-gray-700 peer-focus:ring-4 peer-focus:ring-green-300 dark:peer-focus:ring-green-800 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-0.5 after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-green-600"></div>
                                  <span class="ml-3 text-sm font-medium text-gray-900 dark:text-gray-300">Public?</span>
                                </label>                
                              </div>

                  </div>

                <SectionTitleLineWithButton :icon="mdiTable" title="Table" />
                <div class="w-full py-4 flex justify-center text-center inline-flex items-center">
                  <div class="px-2 w-1/2">
                          <!-- COINS -->
                              <VueMultiselect
                              v-model="selectedCoins"
                              :options="coinOptions"
                              track-by="symbol" label="name" 
                              :searchable="true"
                              placeholder="Coins to track: Bitcoin, Ethereum, Solana, ..." 
                              :multiple="true"
                              :close-on-select="false"
                              :allow-empty="false" 
                              />
                    </div>
                    <div class="px-2 w-1/2">
                    <!-- METRICS -->
                              <VueMultiselect 
                              class="dark:bg-gray-700"
                              v-model="selectedMetrics" 
                              deselect-label="Click to remove" 
                              :multiple="true" 
                              track-by="id" label="label" 
                              placeholder="Metrics to track: Price, TVL, fees, social activity, ..." 
                              :options="metricsOptions" 
                              :searchable="true" 
                              :close-on-select="false"
                              :allow-empty="false" />
                            </div>
                </div>
                <SectionTitleLineWithButton :icon="mdiChartLine" title="Charts" />
                <div class="w-full py-4 flex justify-center text-center inline-flex items-center">
                            <div class="w-1/2 px-2"> 
                              <input type="text" id="disabled-input" aria-label="disabled input" class="mb-6 bg-gray-100 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 cursor-not-allowed dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-gray-400 dark:focus:ring-blue-500 dark:focus:border-blue-500" value="TVL Charts" disabled>
                            </div>

                            <div class="px-2 w-1/2">
                              <input type="text" id="disabled-input" aria-label="disabled input" class="mb-6 bg-gray-100 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 cursor-not-allowed dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-gray-400 dark:focus:ring-blue-500 dark:focus:border-blue-500" value="Fee Charts" disabled>
                            </div>
                </div>


                    <!--SUBMIT-->
                            <div class="py-2 flex justify-center">
                              <button type="submit" class="text-gray-900 bg-white hover:bg-gray-100 border border-gray-200 focus:ring-4 focus:outline-none focus:ring-gray-100 font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center dark:focus:ring-gray-600 dark:bg-gray-800 dark:border-gray-700 dark:text-white dark:hover:bg-gray-700 mr-2 mb-2">
                                <img class="w-4 h-4 mr-2" src="https://cdn3.iconfinder.com/data/icons/simple-web-navigation/165/tick-512.png" alt="plus">
                                  Create dashboard
                              </button>
                            </div>
                  </form>
              </CardBox>
            </div>
  </div>
</template>
<script>
import VueMultiselect from 'vue-multiselect'
import CardBox from "@/components/CardBox.vue";
import MyDashboardsIntro from "./MyDashboardsIntro.vue";
import NotificationBar from "@/components/NotificationBar.vue";
import SuccessError from '@/components/SuccessError.vue';
import { supabase } from "../supabase";
import { metricOptions } from "@/stores/metricOptions";
import { checkAvailableCoins, getUserInfo, generateJSON } from "@/stores/supastore";
import SectionTitleLineWithButton from "@/components/SectionTitleLineWithButton.vue";
import { mdiTable, mdiMonitorDashboard, mdiHomeAccount, mdiChartLine } from '@mdi/js';

export default {
  components: { 
    VueMultiselect,
    CardBox,
    MyDashboardsIntro,
    NotificationBar,
    SuccessError,
    SectionTitleLineWithButton
   },
  data() {
    return {
      mdiTable,
      mdiMonitorDashboard,
      mdiHomeAccount,
      mdiChartLine,
      success: false,
      error: null,
      dashboardName: null,
      dashboardDescription: null,
      publicDashboard: false,
      user: {},
      selectedCoins: null,
      coinOptions: [],
      selectedMetrics: null,
      metricsOptions: metricOptions,
    };
  },
  async created() {
    this.user = await getUserInfo();
    this.coinOptions = await checkAvailableCoins();
  },
  methods: {
    submitForm() {
      if (!this.selectedCoins || !this.selectedMetrics) {
        this.error = "Please select at least one coin and one metric before submitting.";
        return;
      }
      this.error = null;
      this.updateDatabase();
    },
    async updateDatabase() {
      // Get selected coins and metrics
      const selectedCoins = this.selectedCoins;
      const selectedMetrics = this.selectedMetrics;
      const dashboardName = this.dashboardName;
      const dashboardDescription = this.dashboardDescription;
      console.log(selectedCoins, selectedMetrics, dashboardName, dashboardDescription);

      const jsonData = generateJSON(dashboardName, dashboardDescription, selectedCoins, selectedMetrics);
      console.log("jsonData ", jsonData);

      // Make HTTP request to Supabase API to update the database
      try {
        const { error } = await supabase
          .from("dashboard")
          .insert({
            json: jsonData,
            userid: this.user.id,
            public: this.publicDashboard,
          });
        this.success = true;
        setTimeout(() => {
          this.$router.push('/mydashboards');
        }, 2000);


      } catch (error) {
        console.error(error);
        this.error = error.message;
      }
    },
  },
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.css"></style>

